.addcart{
    height: 100%;
    /* max-width: 30%; */
    width: 40%;
    overflow: auto;
    /* background-color: rgb(192, 246, 255);     */
    /* background: rgb(24, 90, 212);  */
  background: linear-gradient(180deg, rgba(20, 54, 117, 0.681) 30%, rgba(214, 80, 181, 0.873) 60%); 
    border: 1px solid black;
    background-color:none;
    justify-content: space-around;

}
.addcart::-webkit-scrollbar {
    display: none;
}
  .header{
    border: 2px solid black;
    justify-content: space-around;
    display: flex;
    text-align: center;
    /* margin: 5px; */
    padding: 10px;
    /* border-radius: 10px; */
    align-items: center;
    /* background-color: #45FFCA; */
    background: linear-gradient(60deg, #FFB6D9 0%, #687EFF 100%);  

    /* background: #4edad0; */
  
    >h2{
        padding-left: 3px;
        font-size: 30px;
    }
}
.list2{
    display: flex;
    /* background: #4edad0; */
    border-radius:5px ; 
    /* background-color:#45FFCA; */
    padding: 3px;
    justify-content: space-around;
}

.headicon,.count{
    font-weight: 800px;
    font-size: 20px;
    border: none;
    text-align: center;
    max-width: 30px;
}
.count{
    font-weight: 800px;
    font-size: 20px;
    border: none;
    text-align: center;
    background: linear-gradient(60deg, #FFB6D9 0%, #687EFF 100%);  
    max-width: 30px;
}

.newimg{
    height: 50px;
    width: 50px;
    }
.cartclose{
    position: absolute;
    /* font-size: 2rem; */
    /* margin-left: 100px; */
    font-weight: bold;
    width: 1rem;
    background-color: red;
    top:4.5rem;
    right: 0.5rem;
    cursor: pointer;
}

.newcart{
    justify-content: space-around;
    margin: 15px;
    display: flex;
    text-align: left;
    align-items: center;    
    border-radius: 5px;
    background: linear-gradient(60deg, #FFB6D9 20%, #687EFF 60%);  
    padding: 5px;
    /* justify-content: center; */
    >h5{
       text-decoration: line-through;
       font: lighter;    
    }
}
.but{
   border: none;
   /* background-color:white; */
   font-size: 25px;
   color:black;
   cursor: pointer;
   background: linear-gradient(60deg, #096ef3 20%, #f303fc 60%);  

}
.total{
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: sticky;
    position: -webkit-sticky;
    bottom: 0px;
    padding: 15px;
    margin-top:77vh;
    font-size: 15px;
    font-weight: 600;
    text-align: right;
    color: black;
    border-top: 3px solid black;
    background: linear-gradient(60deg, #FFB6D9 0%, #687EFF 100%);  

}
.totalnum{
    font-size: 20px;
    border: none;
    max-width: 40px;
}
.countitam1{
    max-width: 15px;
    align-items:center;
    font-size: larger;
    justify-content: center;
    border: none;
    /* color: aqua*/
    background: #096ef3;
}
.numbutton{
    font-size: larger;
    padding: 1px;
    /* background-color:  #687EFF ; */
    background: linear-gradient(60deg, #096ef3 20%, #f303fc 60%);  

}
