.homepage{
    height: 90vh;  
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;
    display: flex;
  }
  .snow,.snow:after,.snow:before{
    content: "";
    position: absolute;
    top: -650px;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: 
    radial-gradient(4px 4px at 100px 50px, #fff, transparent),
    radial-gradient(6px 6px at 200px 150px, #fff, transparent),
    radial-gradient(3px 3px at 300px 250px, #fff, transparent),
    radial-gradient(4px 4px at 400px 350px, #fff, transparent),
    radial-gradient(6px 6px at 500px 100px, #fff, transparent),
    radial-gradient(3px 3px at 50px 200px, #fff, transparent),    
    radial-gradient(4px 4px at 150px 300px, #fff, transparent),
    radial-gradient(6px 6px at 250px 400px, #fff, transparent),
    radial-gradient(3px 3px at 350px 500px, #fff, transparent);
    background-size: 650px 650px;
    z-index: 1;
    animation: snowAnim 3s linear;
    animation-iteration-count: infinite;
  }
  .snow:after{
    margin-left: -250px;
    opacity: 0.5;
    filter: blur(2px);
    animation-duration: reverse; 
    animation-duration: 6s;
  }
  .snow:befoer{
    margin-left: -250px;
    opacity: 0.7;
    filter: blur(2px);
    animation-duration: reverse; 
    animation-duration: 9s;
  }
  @keyframes snowAnim{
    from{
      transform: translateY(0);
    }
    to{
      transform: translateY(650px);
    }
  }
  

.homecon-right{
  height: 90vh;
  width: 50%;
  text-align: start;  
}

.home-header{
    font-size: 40px;
    margin: 80px 0px 0px 60px ;
    top: 200px;
    background: transparent;
    >h3{
    background: linear-gradient(180deg,rgba(12, 173, 201, 0.911), rgb(193, 112, 240) 50%, rgba(10, 181, 223, 0.705) 90%); 
    text-shadow: 0px 2px 2px rgba(2, 8, 8, 0.774),
                0px 5px 5px rgba(0, 0, 0, 0.774),
                0px 8px 2px rgba(0, 0, 0, 0.774); 
   color: rgb(188, 57, 240);
   background-clip: text;
 }
  >p{
    font-weight: 600;
    font-size: 20px;
    margin-top: 5px;
  }
} 
.home-btn{
  padding: 10px;
  background-color: rgb(22, 216, 22);
  cursor: pointer;
  color: white;
  border-radius: 5px;
  border: none;
  font-weight: 700;
  letter-spacing: 0.1em;
}
.homecon-left{
  /* background-color: black;   */
  height: 90vh;
  width: 50%;
  /* background: linear-gradient(180deg,rgba(255, 255, 255, 0.788), rgba(4, 145, 163, 0.568) );  */
}
.homeleft-box{
  position: absolute;
  align-items: center;
  justify-content: center;
  height: 500px;
  width: 500px;
  margin: 60px 0px 0px 90px;
  border-radius: 50%;
  box-shadow: 0px 0px 3px 1px rgb(199, 78, 255),
              0px 0px 8px 5px rgb(125, 11, 153),
              0px 0px 18px 8px rgb(87, 18, 94) ; 
  background: linear-gradient(90deg,rgba(219, 155, 214, 0.842), rgba(171, 20, 209, 0.795) ); 
}
.homeleft-imgbox{
  height: 450px;
  width: 450px;
  border-radius: 50%;
  margin: 25px 0px 0px 28px;
  background: linear-gradient(90deg,rgba(107, 17, 143, 0.884), rgb(206, 116, 212) ); 
  box-shadow: 0px 0px 3px 1px rgb(199, 78, 255),
              0px 0px 8px 5px rgb(125, 11, 153),
              0px 0px 18px 8px rgb(87, 18, 94) ; 
}
.home-img{
  height: 440px;
  width: 440px;
  border-radius: 50%;
  margin-top: 5px;
  transition: 0.3s;
}
.home-img:hover{
  transform: scale(1.1);
  cursor: pointer;
}
.gifts{
  display: flex;
  gap: 15px;
  margin-top:40px;
  margin-left: 60px;
  height: 120px;
}
.gift{
  display: flex;
  position: relative;
  justify-content: center;
  align-items:center ;
  height: 100px;
  width: 100px;
  border-radius: 10px;
  background-color: white;
  /* background: transparent; */
  backdrop-filter: blur(15px);
  box-shadow: 0px 0px 1px 1px rgb(226, 73, 226),
              0px 0px 2px 2px rgb(204, 70, 204),
              0px 0px 3px 3px rgb(145, 46, 175) ; 
}
.giftimg{
  padding: 5px;
  height: 90px;
  width: 90px;
  transition: 0.3s;
  filter: drop-shadow( 0px 1px 5px rgb(203, 82, 214));
}
.giftimg:hover{
  transform: scale(1.1);
  cursor: pointer;
}

@media  screen and (max-width:600px) {
 .homepage{
    display: grid;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
 }
 .homecon-right{
  display: grid;
  height: 100%;
  width: 100%;
 }
 .home-header{
  margin: 10px 0px 0px 10px;
 }
 .homeleft-box{
  margin: 30px 0px 0px 15px;
  height: 450px;
  width: 450px;
}
.homeleft-imgbox{
  height: 400px;
  width: 400px;
}
.home-img{
  height: 380px;
  width: 380px;
}
.gifts{
  display: flex;
  gap: 15px;
  position: absolute;
  /* margin-top:100px; */
  margin-left: 20px;
  height: 110px;
  bottom: 0px;
}
}