:root {
  text-align: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("e-com11.webp");
}

.App {
  display: flex;
  height: 90vh;
  /* background: linear-gradient(180deg,rgba(255, 255, 255, 0.192), rgba(10, 226, 255, 0.082) ); */
}

.product::-webkit-scrollbar {
  display: none;
}

.product {
  height: 100%;
  width: min(100%,1500px);
  overflow: auto;
  /* min-height: 0%; */
}

.search {
  margin-top: 50px;
}

::placeholder {
  color: #def3fa;
}

.searchbox {
  font-size: 1rem;
  padding: 0.5rem 2rem;
  border: none;
  outline: none;
  border-radius: 0.4rem 0rem 0rem 0.4rem;
  cursor: auto;
  text-transform: uppercase;
  background-color: rgb(14, 14, 26);
  color: #def3fa;
  font-weight: 700;
  transition: 0.6s;
}

.searchbox:active {
  scale: 0.92;
}

.searchbox:hover {
  background: rgb(2, 29, 78);
  background: linear-gradient(270deg, rgba(2, 29, 78, 0.681) 0%, rgba(31, 215, 232, 0.873) 60%);
  /* color: rgb(4, 4, 38); */
}

.inputin {
  align-items: center;
    font-size: 1rem;
  padding: 0.4rem 1rem 0.5rem 1rem;
  border: none;
  outline: none;
  border-radius: 0.4rem;
  border-radius: 0rem 0.4rem 0.4rem 0rem;
  cursor: pointer;
  text-transform: uppercase;
  background-color: rgb(14, 14, 26);
  color: rgb(234, 234, 234);
  font-weight: 700;
  transition: 0.6s;
}

.inputin:active {
  scale: 0.92;
}

.inputin:hover {
  background: rgb(2, 29, 78);
  background: linear-gradient(270deg, rgba(2, 29, 78, 0.681) 0%, rgba(31, 215, 232, 0.873) 60%);
  color: rgb(4, 4, 38);
}

.productlist {
  justify-content: center;
  margin-top: 40px;
} 
.img {
  height: 100px;
  width: 100px;
  filter: drop-shadow(10px 10px 10px);
}


.carts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  font-size: 20px;
  font-weight: 500;
  list-style: none;
  
}

.cart {
  border-radius: 50px;
  /* width: 300px; */
  text-align: center;
  border: 2px solid black;
  padding: 10px 10px 10px 10px;
  border-radius: 20px;
  margin: 10px 20px 10px 20px;
  backdrop-filter: blur(15px);
  background: transparent;
  justify-content: space-around;
}

.cart:hover {
  transition: 1s;
  transform: scale( 1.02);
  cursor: pointer;
  color: rgb(4, 4, 38);
  background: rgb(71, 161, 202);
  background: linear-gradient(135deg, rgba(130, 17, 175, 0.681) 0%, rgba(205, 167, 221, 0.873) 60%);

}

.reating {
  font-weight: 500;
  font-size: 20px;
  /* color: rgb(0, 255, 242); */
  color: black;
  padding: 0.7em 1.4em 0.7em 1.1em;
  background:transparent  ;
  /* background: linear-gradient(0deg, rgb(17, 228, 207) 0%, rgb(28, 83, 90) 100%); */
  padding: 0.2em 0.2em 0.2em 0.2em;
}

.reating:hover {
  color: black;
}

.cart-button {
  width: 5em;
  position: relative;
  height: 2.5em;
  border: 3px ridge #d211e4;
  outline: none;
  background-color: transparent;
  color: rgb(0, 0, 0);
  transition: 1s;
  border-radius: 0.3em;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  margin-top: 5px;
}

.cart-button::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 3%;
  width: 95%;
  height: 4%;
  background-color: #77076e;
  transition: 0.5s;
  transform-origin: center;
}

.cart-button::before {
  content: "";
  transform-origin: center;
  position: absolute;
  top: 100%;
  left: 3%;
  width: 95%;
  height: 4%;
  background-color: #a30a75;
  transition: 0.5s;
}

.cart-button:hover::before,
button:hover::after {
  transform: scale(0)
}

.cart-button:hover {
  box-shadow: inset 0px 0px 25px #060f1a;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

.price {
  margin-top: 8px;
  margin-bottom: 8px;
  >h5 {
    margin-top: 2px;
    padding-left: 8px;
    text-decoration: line-through;
  }
}

.opencart {
  background-color: #c9b4ccbb;
  backdrop-filter: blur(15px);
  position: absolute;
  font-size: 1.5rem;
  margin-left: 20px;
  font-weight: 700;
  top: 5rem;
  left: 0.5rem;
  cursor: pointer;
}