*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
 }
  .nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color:black;
    height: 65px;
    border-bottom: 1px solid black ;
    background: transparent;
    background: linear-gradient(90deg,rgb(183, 59, 221)20%,rgb(112, 189, 240),rgba(214, 62, 228, 0.993) ); 
    >h2{
       margin-left: 10px;
       background: linear-gradient(180deg,rgba(12, 173, 201, 0.911), rgb(193, 112, 240) 50%, rgba(10, 181, 223, 0.705) 90%); 
       text-shadow: 0px 2px 2px rgba(2, 8, 8, 0.774),
                   0px 5px 5px rgba(7, 14, 14, 0.774),
                   0px 8px 2px rgba(8, 8, 8, 0.774); 
      /* color: rgb(232, 59, 255); */
      color: white;
      background-clip: text;
    }
 }
  .nav-link{
    display: flex;
    list-style: none;
    justify-content: flex-end;
    width: 50% ;
 }
  .home,.products,.contact,.login{
    /* text-decoration: none; */
    padding: 5px;
    margin: 20px;
    /* color:black;  */
    font-size: 25px;
    cursor: pointer;
    color: white;
    text-decoration: none;
    font-size: 25px;
    border: none;
    background: none;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
  }
  .menu{
    display: none;
    font-size: 30px;
    font-weight: bold;
    list-style: none;
  }

 
  @media  screen and (max-width:600px) {
        .menu{
          display: flex;
          margin-right: 20px;
          color: rgba(214, 21, 231, 0.89);
          background-color: #100720;
                  
        }    
        .nav-link{
         display: none;
        }
       .mobilenav-link{
         position: absolute;
         text-align: center;
         margin-top: -10px;
         align-items: center;
         padding: 10px;
         list-style:none ;
         border-radius: 10%;
         background: transparent;
         backdrop-filter: blur(14px);
         width: 50%;
         height: max-content;
         top: 85px;
         right: 5px;
         z-index: 1;
         border: solid 2px black;
        }
      .mobilenav-link li{
         margin: 40px;
        
      }
        .home,.products,.contact,.login{
           text-align: center;
           padding: 15px; 
           width: 100%; 
           color:black; 
           transition: all 0.5s ease;
       }
      }
  .home{
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 600;
  }
  .home:hover {
    border-bottom: 3px solid rgb(99, 36, 99);
    }
  .products {
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
    }
  .products:hover {
    border-bottom: 3px solid rgb(99, 36, 99);
  }
  .contact {
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
  }
  .contact:hover {
    border-bottom: 3px solid rgb(99, 36, 99);
  } 
  
  .login {
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
  }
  .login:hover {
    border-bottom: 3px solid rgb(99, 36, 99);
   }
 