.loginpage-fooder{
    margin-top: 50px;
    border-top: 1px solid black;
    display: flex;
    justify-content: space-around;
     padding: 10px;
}
.loginpage-button{
    background: linear-gradient(0deg, rgba(20, 167, 62, 1) 0%, rgba(102, 247, 113, 1) 100%);
    border-radius: 20em;
    border: none;
    padding: 0.7em 1.4em 0.7em 1.1em;
    cursor: pointer;
    margin: 12px;
    text-decoration: none;
    color: black;
}